<template>
    <div class="food_list" v-if="index == 0">
        <van-tree-select :main-active-index.sync="active" height="88vw" :items="items" @click-nav="navClick">
            <template #content>
                <div class="item_bg" v-for="(i, index) in subItem" :key="index">
                    <FoodAdd :item="i" :showAdd="true" :addClick="addClick" :onChange="onChange" />
                </div>
            </template>
        </van-tree-select>
    </div>
</template>
<script>
import { reactive, toRefs } from "vue"
import FoodAdd from "@/components/FoodAdd.vue"
export default {
    props: ["index", "foodData"],
    components: {
        FoodAdd
    },
    setup(props) {
        let data = reactive({
            items: [],
            active: 0,
            subItem: []
        })

        const init = () => {
            let newList = [];
            props.foodData.items.map((i, index) => {
                newList.push({ text: i.text })
                if (data.active == index) {
                    data.subItem = i.children
                }
            })
            data.items = newList
        }
        init()

        const navClick = (i) => {
            data.active = i
            init()
        }

        const addClick = (i) => {
            data.subItem.forEach(item => {
                if (item.id == i) {
                    item.add = false
                    item.num += 1
                }
            })
        }

        const onChange = (value, detail) => {
            if (value == 0) {
                data.subItem.forEach(item => {
                    if (item.id == detail.name) {
                        item.add = true
                    }
                })
                return
            }
            data.subItem.forEach(item => {
                if (item.id == detail.name) {
                    item.num = value
                }
            })
        }

        return {
            ...toRefs(data),
            navClick,
            addClick,
            onChange
        }
    }
}
</script>
<style scoped lang="less">
.food_list {
    margin-top: 20px;

    .item_bg {
        padding: 10px;
    }
}

/deep/ .van-sidebar-item--select::before {
    background-color: #ffc400;
}
</style>